import React, { Component } from "react";

export class Progress extends Component {
    constructor(props) {
        super(props);
        this.state = {
            skills: []
        };
    }
    componentDidMount() {
        let url = "../../../scripts/skills.json";
        // console.log("url: ", url);
        fetch(url)
            .then(response => {
                // console.log("response: ", response);
                return response.json();
            })
            .then(d => {
                // console.log("data: ", d);
                this.setState({ skills: d });
                // console.log("state", this.state.skills);
            })
            .catch(error => console.log("Error: ", error));
    }
    render() {
        // const { skillsData } = this.props;
        if (this.state.skills && this.state.skills.length > 0) {
            return (
                <div className="row">
                    {this.state.skills.map((skill, i) => (
                        <div className="col-md-6 animate-box" key={i}>
                            <div className="progress-wrap ftco-animate fadeInUp ftco-animated">
                                <h3>{skill.tech}</h3>
                                <div className="progress">
                                    <div
                                        className="progress-bar color-3"
                                        role="progressbar"
                                        aria-valuenow={skill.rate}
                                        aria-valuemin="0"
                                        aria-valuemax={skill.rate}
                                        style={{ width: `${skill.rate}%` }}
                                    >
                                        <span>{`${skill.rate}%`}</span>
                                    </div>
                                </div>
                            </div>
                        </div>
                    ))}
                </div>
            );
        } else {
            return (
                <div className="row">
                    <div className="col-12 text-center">
                        Currently, there are no results found!
                    </div>
                </div>
            );
        }
    }
}

export default Progress;
