import React, { Component } from "react";

export class Jobs extends Component {
    constructor(props) {
        super(props);
        this.state = {
            jobs: []
        };
    }

    componentDidMount() {
        let url = "../../../scripts/projects.json";
        console.log("projects url: ", url);
        fetch(url)
            .then(response => {
                console.log("response: ", response);
                return response.json();
            })
            .then(job => {
                console.log("data: ", job);
                this.setState({ jobs: job });
                console.log("state", this.state.jobs);
            })
            .catch(error => console.log("Error: ", error));
    }

    render() {
        if (this.state.jobs && this.state.jobs.length) {
            return (
                <div className="row">
                    {this.state.jobs.map(job => (
                        <div className="col-md-4" key={job.id}>
                            <div
                                className="project img ftco-animate d-flex justify-content-center align-items-center fadeInUp ftco-animated"
                                style={{
                                    backgroundImage: `url("../${job.bgImg}")`
                                }}
                            >
                                <div className="overlay"></div>
                                <div className="text text-center p-4">
                                    <h3>
                                        <a href={`${job.url}`} target="_blank">
                                            {job.title}
                                        </a>
                                    </h3>
                                    <span>Web Design</span>
                                </div>
                            </div>
                        </div>
                    ))}
                </div>
            );
        } else {
            return (
                <div className="row">
                    <div className="col-12 text-center">
                        Currently, there are no results found!
                    </div>
                </div>
            );
        }
    }
}

export default Jobs;
